@import url(https://fonts.googleapis.com/css2?family=Great+Vibes&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Quicksand&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Dancing+Script:wght@600&display=swap);
@import url(https://fonts.googleapis.com/css?family=Montserrat:400,800);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#root {
  height: 100%;
}

* {
  margin: 0;
  box-sizing: border-box;
}

body {
  background-color: #f5f6fa;
  overflow-x: hidden;
}

.cardfan {
  width: 50%;
  margin: 0 auto;
  position: relative;
  margin-top: -100px;
}

.cardfan img {
  position: absolute;
  width: 100%;
  height: auto;
  border: 12px solid #ffe;
  box-shadow: 4px 4px 3px rgba(0, 0, 0, 0.2);
  -webkit-transform-origin: center 460px;
          transform-origin: center 460px;
  transition: all 1s linear;
  float: right;
}
.cardfan img:first-child {
  -webkit-transform: rotate(5deg);
          transform: rotate(5deg);
}
.cardfan img:last-child {
  -webkit-transform: rotate(-5deg);
          transform: rotate(-5deg);
}
.cardfan:hover img:first-child {
  -webkit-transform: rotate(20deg);
          transform: rotate(20deg);
}
.cardfan:hover img:last-child {
  -webkit-transform: rotate(-20deg);
          transform: rotate(-20deg);
}

img#wine2 {
  -webkit-filter: grayscale(100%);
  filter: grayscale(100%);
  -webkit-filter: url(#greyscale);
          filter: url(#greyscale);
  -webkit-filter: gray;
          filter: gray;
}
img#wine2:hover {
  -webkit-filter: grayscale(0);
  filter: grayscale(0);
  -webkit-filter: none;
          filter: none;
  z-index: 1;
}
img#wine1 {
  -webkit-filter: sepia(100%);
  filter: sepia(100%);
  -webkit-filter: url(#sepia);
          filter: url(#sepia);
  background-color: #5e2612;
  filter: alpha(opacity = 50);
}
img#wine1:hover {
  -webkit-filter: sepia(0);
  filter: sepia(0);
  filter: alpha(opacity = 100);
  -webkit-filter: none;
          filter: none;
}
img#wine3 {
  -webkit-filter: blur(3px);
  filter: blur(3px);
  -webkit-filter: url(#blur);
          filter: url(#blur);
}
img#wine3:hover {
  -webkit-filter: blur(0px);
  filter: blur(0px);
  -webkit-filter: none;
          filter: none;
  z-index: 1;
}

.spreadCardfan img:first-child {
  -webkit-transform: rotate(20deg);
          transform: rotate(20deg);
}
.spreadCardfan img:last-child {
  -webkit-transform: rotate(-20deg);
          transform: rotate(-20deg);
}
.spreadCardfan img#wine1 {
  -webkit-filter: sepia(0);
  filter: sepia(0);
  filter: alpha(opacity = 100);
  -webkit-filter: none;
          filter: none;
}

@media screen and (max-width: 1700px) {
  .cardfan {
    height: 200px;
    margin-bottom: -350px; 
  }
}

@media screen and (max-width: 1300px) {
  .cardfan {
    width: 70%;
    margin-top: 50px;
    height: 420px;
  }
}

@media screen and (max-width: 1200px) {
  .cardfan {
    height: 250px;
  }
}

@media screen and (max-width: 800px) {
  .cardfan {
    height: 120px;
    margin-top: 0px;
  }
}

@media screen and (max-width: 480px) {
  .cardfan {
    height: 100px;
    margin-top: 10px;
  }
  .cardfan img {
    -webkit-transform-origin: center 250px;
            transform-origin: center 250px;
    border: 6px solid #ffe;
  }
}

.container {
  margin: auto;
  margin-top: 70px;
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
  position: relative;
  overflow: hidden;
  width: 70%;
  max-width: 100%;
  min-height: 550px;
}

.form-container {
  position: absolute;
  top: 0;
  height: 100%;
  transition: all 0.6s ease-in-out;
}

.sign-in-container {
  left: 0;
  width: 50%;
  z-index: 2;
  opacity: 1;
}

.container.right-panel-active .sign-in-container {
  -webkit-transform: translateX(100%);
          transform: translateX(100%);
}

.sign-up-container {
  left: 0;
  width: 50%;
  opacity: 0;
  z-index: 1;
}

.container.right-panel-active .sign-up-container {
  -webkit-transform: translateX(100%);
          transform: translateX(100%);
  opacity: 1;
  z-index: 5;
  -webkit-animation: show 0.6s;
          animation: show 0.6s;
}

@-webkit-keyframes show {
  0%,
  49.99% {
    opacity: 0;
    z-index: 1;
  }

  50%,
  100% {
    opacity: 1;
    z-index: 5;
  }
}

@keyframes show {
  0%,
  49.99% {
    opacity: 0;
    z-index: 1;
  }

  50%,
  100% {
    opacity: 1;
    z-index: 5;
  }
}

.overlay-container {
  /* display: none; ************************* */
  position: absolute;
  top: 0;
  left: 50%;
  width: 50%;
  height: 100%;
  overflow: hidden;
  transition: -webkit-transform 0.6s ease-in-out;
  transition: transform 0.6s ease-in-out;
  transition: transform 0.6s ease-in-out, -webkit-transform 0.6s ease-in-out;
  z-index: 100;
}

.container.right-panel-active .overlay-container {
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
}

.overlay {
  background: #b8001f;
  background: linear-gradient(to right, #bc8034, #b8001f);

  background-repeat: no-repeat;
  background-size: cover;
  background-position: 0 0;
  color: #ffffff;
  position: relative;
  left: -100%;
  height: 100%;
  width: 200%;
  -webkit-transform: translateX(0);
          transform: translateX(0);
  transition: -webkit-transform 0.6s ease-in-out;
  transition: transform 0.6s ease-in-out;
  transition: transform 0.6s ease-in-out, -webkit-transform 0.6s ease-in-out;
}

.container.right-panel-active .overlay {
  -webkit-transform: translateX(50%);
          transform: translateX(50%);
}

.overlay-panel {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 0 40px;
  text-align: center;
  top: 0;
  height: 100%;
  width: 50%;
  -webkit-transform: translateX(0);
          transform: translateX(0);
  transition: -webkit-transform 0.6s ease-in-out;
  transition: transform 0.6s ease-in-out;
  transition: transform 0.6s ease-in-out, -webkit-transform 0.6s ease-in-out;
}

.overlay-left {
  -webkit-transform: translateX(-20%);
          transform: translateX(-20%);
}

.container.right-panel-active .overlay-left {
  -webkit-transform: translateX(0);
          transform: translateX(0);
}

.overlay-right {
  right: 0;
  -webkit-transform: translateX(0);
          transform: translateX(0);
}

.container.right-panel-active .overlay-right {
  -webkit-transform: translateX(20%);
          transform: translateX(20%);
}

.social-container {
  margin: 20px 0;
}

.social-container a {
  border: 1px solid #dddddd;
  border-radius: 50%;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  margin: 0 5px;
  height: 40px;
  width: 40px;
}

.back-to-sign-in {
  display: none;
}

.no-account-container {
  display: none;
}

.tag {
  color: #333;
  font-size: 14px;
  text-decoration: none;
  margin: 15px 0;
  cursor: pointer;
}

@media screen and (max-width: 1200px){
  .container {
    width: 85%;
  }
}

@media screen and (max-width: 900px) {
  .overlay-container {
    display: none;
  }

  .sign-in-container-hide {
    display: none;
  }

  .sign-in-container-active {
    width: 100%;
    opacity: 1;
  }

  .sign-up-container-active {
    width: 100%;
    opacity: 1;
  }

  .sign-up-container-hide {
    display: none;
  }

  .back-to-sign-in {
    display: block;
  }

  .no-account-container {
    display: block;
  }
}

@media screen and (max-width: 520px) {
  .container {
    width: 95%;
    margin-top: 35px;
  }
}

@media screen and (max-width: 600px) {
  .container {
    margin-top: 50px;
  }
}

